@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.Display{font-family:Jost;font-size:36px;color:white;width:5vw;text-align:center}

.Timer{font-family:Jost;font-size:36px;color:white;width:5vw}

.Words{font-family:Jost;font-size:24px;text-align:left;width:50%}.Line{-webkit-animation:grow-height 0.2s;animation:grow-height 0.2s;max-height:45px;overflow:hidden}.notTyped{display:inline-block;overflow:hidden;transition:0.2s}.correct{display:inline-block;overflow:hidden;transition:0.2s}.incorrect{display:inline-block;overflow:hidden;max-width:20px;-webkit-animation:grow-width 0.2s;animation:grow-width 0.2s}@-webkit-keyframes grow-width{0%{max-width:0}}@keyframes grow-width{0%{max-width:0}}@-webkit-keyframes grow-height{0%{max-height:0}}@keyframes grow-height{0%{max-height:0}}

.Speed{font-family:Jost;font-size:36px;color:white;text-align:right;width:5vw}

.container{display:flex;width:100vw;height:100vh;justify-content:center}.Words{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);-webkit-animation:fadeIn 1s;animation:fadeIn 1s}.info{display:flex;width:95vw;justify-content:space-between;-webkit-animation:fadeIn 3s;animation:fadeIn 3s}@-webkit-keyframes fadeIn{0%{opacity:0}50%{opacity:0}}@keyframes fadeIn{0%{opacity:0}50%{opacity:0}}

